<template>
    <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title">{{ modalHeader }} Listing</h5>
                        <a title="Add Skill" href="javascript:;" class="p-action-btn text-white float-right"
                            @click="openCredentialPopup(modalHeader)"><i class="fa fa-plus"></i><br><span>Add</span></a>
                    </slot>
                    <button type="button" @click="$emit('Close')" title="" class="close" data-original-title="Close"><em
                            class="fa fa-times" aria-hidden="true"></em></button>
                </div>

                <div class="modal-body">
                    <div class="col-7 float-left pr-1 pl-0">
                        <div class="form-group">
                            <input id="search" maxlength="50" type="text" v-model="searchInput"
                                placeholder="Search by UserName" autocomplete="off" class="form-control search">
                        </div>
                    </div>
                    <div class="col-5 float-left pl-0">
                        <div class="background-0 text-left form-check-inline">
                            <input type="hidden" id="hdnSearch" value="">
                            <a title="" id="Go" href="javascript:;" @click="searchByUsername"
                                class="btn btn-success mr-1 px-4 hlsearch"><i class="fa fa-search"></i></a>
                            <a href="javascript:;" title="" @click="refresh" class="btn btn-danger clearsearchtext px-4"><i
                                    class="fa fa-refresh"></i></a>
                        </div>
                    </div>
                    <tg-list v-if="modalHeader == 'SMTP'" :IsShowAction="true" ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
                        :RenderRowActionMethod="renderActions" v-on:ActionButtonClick="actionButtonClick"
                        IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')" :listType="['List']"
                        :ListData="CredentialList" :HeaderData="Headers" :TotalRecords="totalRecords"
                        :ListDataCallBackFunction="FetchRecord" :showCheckBox="false" :SortExp="SortExp" :SortBy="SortBy"
                        :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
                        v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata">
                    </tg-list>
                    <tg-list v-if="modalHeader == 'MailChimp'" :IsShowAction="true" ModuleName="CRM"
                        SubModuleCode="CRM_CAMPAIGN" :RenderRowActionMethod="renderActions"
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')"
                        :listType="['List']" :ListData="CredentialList" :HeaderData="MailChimpHeaders"
                        :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord" :showCheckBox="false"
                        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                        v-on:SortdataButtonClick="sortdata">
                    </tg-list>
                    <tg-list v-if="modalHeader == 'SendGrid'" :IsShowAction="true" ModuleName="CRM"
                        SubModuleCode="CRM_CAMPAIGN" :RenderRowActionMethod="renderActions"
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')"
                        :listType="['List']" :ListData="CredentialList" :HeaderData="SendGridHeaders"
                        :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord" :showCheckBox="false"
                        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                        v-on:SortdataButtonClick="sortdata">
                    </tg-list>
                    <tg-list v-if="modalHeader == 'Facebook'" :IsShowAction="true" ModuleName="CRM"
                        SubModuleCode="CRM_CAMPAIGN" :RenderRowActionMethod="renderActions"
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')"
                        :listType="['List']" :ListData="CredentialList" :HeaderData="FacebookHeaders"
                        :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord" :showCheckBox="false"
                        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                        v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'key'">
                                <a href="javascript:;" v-on:click="openCredentialPopup('Facebook', data.row)"
                                    :title="data.row[data.column.COLUMN_NAME]">
                                    {{ data.row[data.column.COLUMN_NAME] }}
                                </a>
                            </template>
                        </template>
                    </tg-list>
                    <tg-list v-if="modalHeader == 'Instagram'" :IsShowAction="true" ModuleName="CRM"
                        SubModuleCode="CRM_CAMPAIGN" :RenderRowActionMethod="renderActions"
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')"
                        :listType="['List']" :ListData="CredentialList" :HeaderData="InstagramHeaders"
                        :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord" :showCheckBox="false"
                        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                        v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'key'">
                                <a href="javascript:;" v-on:click="openCredentialPopup('Instagram', data.row)"
                                    :title="data.row[data.column.COLUMN_NAME]">
                                    {{ data.row[data.column.COLUMN_NAME] }}
                                </a>
                            </template>
                        </template>
                    </tg-list>
                    <tg-list v-if="modalHeader == 'Twilio'" :IsShowAction="true" ModuleName="CRM"
                        SubModuleCode="CRM_CAMPAIGN" :RenderRowActionMethod="renderActions"
                        v-on:ActionButtonClick="actionButtonClick" IdentityColumn="REF_PAGE_ID" :HeaderText="$t('Campaign')"
                        :listType="['List']" :ListData="CredentialList" :HeaderData="TwilioHeaders"
                        :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord" :showCheckBox="false"
                        :SortExp="SortExp" :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"
                        v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
                        v-on:SortdataButtonClick="sortdata">
                        <template v-slot:slotdata="{ data }">
                            <template v-if="data.column.COLUMN_NAME == 'key'">
                                <a href="javascript:;" v-on:click="openCredentialPopup('Twilio', data.row)"
                                    :title="data.row[data.column.COLUMN_NAME]">
                                    {{ data.row[data.column.COLUMN_NAME] }}
                                </a>
                            </template>
                        </template>
                    </tg-list>
                </div>
            </div>
        </div>
        <modaladdsmtpVue v-if="showAddSmtpPopup" :ProfileId="ProfileId" @close="close" :SendThrough="modalHeader">
        </modaladdsmtpVue>
        <modalSMSIntegrationVue v-if="showAddMailChimpOrSendGridPopup" :ProfileId="ProfileId" @close="close"
            :SendThrough="modalHeader">
        </modalSMSIntegrationVue>
        <modalFacebookIntegrationVue v-if="showAddFacebookPopup" :ProfileId="ProfileId" @close="close"
            :SendThrough="modalHeader">
        </modalFacebookIntegrationVue>
        <modalTwilioIntegrationVue v-if="showAddTwilioPopup" :ProfileId="ProfileId" @close="close"
            :SendThrough="modalHeader">
        </modalTwilioIntegrationVue>
    </div>
</template>
<script>
import DataService from '../../../services/DataService';
import modaladdsmtpVue from './modaladdsmtp.vue';
import modalSMSIntegrationVue from './modalSMSIntegration.vue';
import modalFacebookIntegrationVue from './modalFacebookIntegration.vue';
import modalTwilioIntegrationVue from './modalTwilioIntegration.vue';
export default {
    components: {
        modaladdsmtpVue,
        modalSMSIntegrationVue,
        modalFacebookIntegrationVue,
        modalTwilioIntegrationVue
    },
    props: {
        modalHeader: {
            type: String,
            required: true
        },
    },
    data() {
        return {
            PageSize: 10,
            PageNumber: 1,
            showAddSmtpPopup: false,
            CredentialList: [],
            ProfileId: 0,
            noRecords: false,
            totalRecords: 0,
            showAddMailChimpOrSendGridPopup: false,
            showAddFacebookPopup: false,
            showAddTwilioPopup: false,
            Headers: [
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "user_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "User Name",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "password",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Password",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "from_email",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Email",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "smtp_port",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP Port",
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "smtp_security",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP_Security",
                    "DISPLAY_ORDER": 5
                }
            ],
            MailChimpHeaders: [
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Profile",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "key",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Key",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "SecretKey",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SecretKey",
                    "DISPLAY_ORDER": 3
                }
            ],
            SendGridHeaders: [
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Profile",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "key",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Api Key",
                    "DISPLAY_ORDER": 2
                }
            ],
            FacebookHeaders: [
                {
                    "COLUMN_NAME": "key",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "key",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "SecretKey",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "App Id",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "user_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Configuration Id",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "URL",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "CallBack Url",
                    "DISPLAY_ORDER": 2
                }
            ],
            InstagramHeaders: [
                {
                    "COLUMN_NAME": "key",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "key",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "SecretKey",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "App Id",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "user_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Configuration Id",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "URL",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "CallBack Url",
                    "DISPLAY_ORDER": 2
                }
            ],
            TwilioHeaders:[
            {
                    "COLUMN_NAME": "key",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "key",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "SecretKey",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Secret Key",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "from_email",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Twilio Phone Number",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "profile Name",
                    "DISPLAY_ORDER": 2
                }
            ],
            listActions: [
                {
                    title: 'Edit',
                    href: 'javascript:void(0)',
                    callbackfunction: 'Edit',
                    icon: 'fa fa-pencil text-success action-icon',
                    additionalSpan: ''
                },
                {
                    title: 'Delete',
                    href: 'javascript:void(0)',
                    icon: 'fa fa-trash',
                    callbackfunction: 'Delete',
                    additionalSpan: ''
                }
            ],
        }
    },
    created: function () {
        var vm = this;
        this.user_id = this.GetUserInfo.ID;
        this.companyId = this.GetUserInfo.CompanyId;
        vm.FetchRecord();
    },
    methods: {
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Edit':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        FillProfileDropdown: function () {

        },
        actionButtonClick(callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'Edit':
                    this.openCredentialPopup(vm.modalHeader, item)
                    break;
                case 'Delete':
                    this.commonDeleteData(item);
                    break;
            }
        },
        commonDeleteData: function (event) {
            var vm = this;
            var val = "";
            val = event.profile_id;
            if (val != 0) {
                vm.confirmR(this.$t("ConfirmDelete"), true, (this.$t("Delete") + "  " + this.$t("Resource")), function () {
                    var postJSON = {
                        ids: val,
                        companyId: vm.companyId,
                        userId: vm.user_id,
                        moduleName: "CRM",
                        refCode: "DELETE_FSM_TICKET_SOURCE"
                    };
                    var objectPostString = JSON.stringify(postJSON);
                    DataService.CommonCommonDelete(objectPostString).then(response => {
                        var box = "";
                        if (response.data[0].Status == "Success") {
                            vm.FetchRecord();
                            box = vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "Success", true, vm.$t('Alert'));
                        }
                        else {
                            box = vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t('Alert'));
                        }
                        setTimeout(function () {
                            box.modal('hide');
                        }, 2000);

                    });
                });
            }
        },
        refresh: function () {
            var vm = this;
            vm.searchInput = '';
            vm.FetchRecord();
        },
        leftCommonSearch() {

        },
        openCredentialPopup: function (dta, item) {
            var vm = this;
            if (item != undefined) {
                vm.ProfileId = item.profile_id;
            } else {
                vm.ProfileId = 0;
            }
            if (dta == 'MailChimp' || dta == 'SendGrid') {
                vm.showAddMailChimpOrSendGridPopup = true;
            } else if (dta == 'Facebook' || dta == 'Instagram') {
                vm.showAddFacebookPopup = true;
            }else if(dta== 'Twilio'){
                vm.showAddTwilioPopup=true
            }
            else {
                vm.showAddSmtpPopup = true;
            }
        },
        searchByUsername: function () {
            var vm = this;
            if (vm.modalHeader == 'Facebook' || vm.modalHeader=='Instagram' || vm.modalHeader=='Twilio') {
                const searchResults = this.CredentialList.filter(profile => {
                    return profile.key.includes(this.searchInput.trim());
                });
                this.CredentialList = searchResults;
                this.totalRecords = this.CredentialList.length;
            } else {
                const searchResults = this.CredentialList.filter(profile => {
                    return profile.smtp_server.includes(this.searchInput.trim());
                });
                this.CredentialList = searchResults;
                this.totalRecords = this.CredentialList.length;
            }
        },
        close: function () {
            var vm = this;
            vm.showAddSmtpPopup = false;
            vm.showAddMailChimpOrSendGridPopup = false;
            vm.showAddFacebookPopup = false;
            vm.showAddTwilioPopup=false;
        },
        ClosemodalManageAdditionalSMTP: function () {
            var vm = this;
            vm.showAddMailChimpOrSendGridPopup = false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecord();
        },
        FetchRecord: function () {
            var vm = this;
            vm.CredentialList = [];
            var url = `profileId=&sourceType=${vm.modalHeader}&pageNumber=${vm.PageNumber}&pageSize=${this.PageSize}`;
            DataService.GetEmailProfile(url).then(function (response) {
                if(response.data.length > 0){ 
                    if (vm.modalHeader == 'Facebook' || vm.modalHeader=='Instagram') {
                    vm.FacebookHeaders.forEach((column) => {
                        if (column.COLUMN_NAME == 'key') {
                            column.settings = {
                                isInSlot: true
                            }
                        }
                    });
                }
                vm.CredentialList = response.data;
                vm.totalRecords = vm.CredentialList[0].TOTAL_RECORDS;
                vm.noRecord = false;
                }else {
                    vm.noRecord = true;
                    vm.CredentialList = [];
                    vm.TotalRecords = 0
                 }
            })
        }
    }
}
</script>
