<template lang="en">
    <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5>{{$t('AddNewAccount')}}</h5>
                    </slot>
                    <button type="button" title="" v-on:click="$emit('close')" class="close" data-original-title="Close">
                        <em class="fa fa-times" aria-hidden="true"></em>
                    </button>
                </div>
                <div class="modal-body">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit" />
                    <div class="col-lg-6">
                        <div class="py-2">
                            <small class="text-danger">{{ $t('MandatoryString') }}</small>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </template>
    
    <script>
    import DataService from "../../../services/DataService";
    export default {
        props:{
            SendThrough: {
                type: String,
                required: true
            },
            ProfileId:{
                type:Number,
                required:false
            }
        },
        data() {
            return {
                FormSchema: [{
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('API Key'),
                            name: "Account",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            visibility:true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Secret Key'),
                            name: "Token",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            visibility:true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('TwilioPhoneNumber'),
                            name: "from_email",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            visibility:true,
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ProfileName'),
                            name: "smtp_server",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true
                            },
                            visibility:true,
                        }
                    ]
                }],
                buttons: [
                {
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                }]
            }
        },
        async created(){
            var vm=this;
            if(vm.ProfileId > 0){
                await vm.GetSMTPProfile(vm.ProfileId);
            }
        },
        methods: {
            async GetSMTPProfile(ths) {
                var vm = this;
                var url = `profileId=${ths}`;
                await DataService.GetEmailProfile(url).then(response => {
                    if(response.data != ''){
                    var IntegrationData = response.data[0];
                    vm.FormSchema[0].Data[0].value = IntegrationData.key;
                    vm.FormSchema[0].Data[1].value = IntegrationData.SecretKey;
                    vm.FormSchema[0].Data[2].value = IntegrationData.from_email;
                    vm.FormSchema[0].Data[3].value = IntegrationData.smtp_server; 
                    }
                });
            },
            onSubmit: function (formfields) {
                var vm = this;
                var jsonObj = { 
                    profile_id: vm.ProfileId,  
                    key: formfields.Account,
                    SecretKey: formfields.Token,
                    send_through: vm.SendThrough, 
                    from_email: formfields.from_email,
                    smtp_server: formfields.smtp_server,            
                }
                var url = JSON.stringify(jsonObj);
                DataService.SaveEmailSetting(url).then(response => {
                    if(response.data=='INSERTED'){
                        vm.$parent.FillProfileDropdown(vm.SendThrough);
                        vm.ShowAlert(vm.$t('AddedSuccess',[vm.$t('AddNewAccount')]),"success", true, vm.$t("Alert"));
                        vm.$parent.FetchRecord();
                        vm.$emit('close');
                    }else if(response.data=='UPDATED'){
                        vm.$parent.FillProfileDropdown(vm.SendThrough);
                        vm.ShowAlert(vm.$t('UpdatedSuccess',[vm.$t('AddNewAccount')]),"success", true, vm.$t("Alert"));
                        vm.$parent.FetchRecord();
                        vm.$emit('close');
                    }else{
                        vm.ShowAlert(vm.$t('AddedError',[vm.$t('AddNewAccount')]),"danger", true, vm.$t("Alert"));
                    }
                });
            }
        }
    }
    </script>
    